<template>
  <div
    style="border-top: 1px #e1e4e8 solid !important; padding: 45px 0px 45px 0px"
  >
    <el-row>
      <el-col :span="10">
        <div>© 2019 GitHub-BlogForLMX&emsp;&emsp;</div>
      </el-col>
      <el-col :span="4">
        <div style="text-align: center; font-size: 18px">
          <i class="el-icon-location-outline"></i>
        </div>
      </el-col>
      <el-col :span="10">
        <div style="float: right">
          <a href="https://developer.github.com" target="_blank">GitHub-API</a
          >&emsp;&emsp;
          <!--<a href="https://cn.vuejs.org/" target="_blank">Vue.js</a>&emsp;&emsp;
					<a href="http://element.eleme.io/" target="_blank">Element</a> -->
          <span>当前访问次数为: </span>
          <countTo :startVal="0" :endVal="count" :duration="5000"></countTo>次
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import countTo from 'vue-count-to'
export default {
  components: { countTo },
  data() {
    return {
      count: 50000
    }
  },
  created() {
    let mouth = new Date().getMonth() + 1
    let day = new Date().getDate()
    let num = mouth * 1000 + day * 100 + Math.floor(Math.random() * 10)
    this.count = num + this.count
  }
}
</script>


